@import "../core/variables";
@import "../index";

.search-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 480px;
  border: 1px solid $brown-grey;
  border-radius: 5px;
  margin-left: 80px;
  padding: 40px;
  box-shadow: 5px 0 5px -4px $brown-grey, -5px 0 5px -4px $brown-grey;
  background-color: $white;
}

@media all and (min-width: 1280px) {
  .search-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 480px;
    border: 1px solid $brown-grey;
    border-radius: 5px;
    margin-left: 80px;
    padding: 40px;
    box-shadow: 5px 0 5px -4px $brown-grey, -5px 0 5px -4px $brown-grey;
    background-color: $white;
  }
}
